.ftpr-app {
  text-align: center;
  background-color: #282c34;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.game-btn,
.game-input,
.game-select {
  padding: 10px;
  border-radius: 6px;
  border: none;
}

.game-btn {
  cursor: pointer;
}
